import { Footer, Navbar } from 'components';
import React, { Component } from 'react';
import { fromEvent } from 'rxjs';
import { map } from 'rxjs/operators';
import { About, Community, Location, OurMenu, Recipes } from 'sections';
import './App.css';

const RequestInfo = (): JSX.Element => {
  return (
    <div
      style={{
        position: 'fixed',
        backgroundColor: 'white',
        color: '#333333',
        fontSize: '18px',
        right: '-20px',
        zIndex: 2,
        width: '100px',
        height: '30px',
        top: 'calc(50% - 50px)',
        cursor: 'pointer',
        transform: 'rotate(270deg)',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center'
      }}
    >
      REQUEST INFO
    </div>
  );
};

const SectionNumbers = (): JSX.Element => {
  const [currentSection, setCurrentSection] = React.useState<number>(0);

  React.useEffect(() => {
    const sub$ = fromEvent(document.body, 'scroll')
      .pipe(map((e: Event) => e))
      .subscribe((e): void => {
        console.log(e);

        // if (e.target > 100) {
        //   setCurrentSection(1);
        // } else {
        //   setCurrentSection(0);
        // }
      });

    return () => sub$.unsubscribe();
  }, []);

  return (
    <div
      style={{
        position: 'fixed',
        left: '27px',
        zIndex: 2,
        height: '110px',
        width: '30px',
        top: 'calc(50% - 60px)',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-around'
      }}
    >
      {new Array(6).fill(null).map((e, index: number) => (
        <div
          className={index === currentSection ? 'seleted-section' : ''}
          style={{
            backgroundColor: '#333333',
            width: index === currentSection ? 'unset' : '15px',
            height: '2px'
          }}
          key={index}
        />
      ))}
    </div>
  );
};

class App extends Component {
  render() {
    return (
      <div className='app-root' id='container-root'>
        <Navbar />

        <RequestInfo />

        <SectionNumbers />

        <About />
        <Community />
        <Location />
        <OurMenu />
        <Recipes />
        <Footer />
      </div>
    );
  }
}

export default App;
